<!--  -->
<template>
<div class=''>
    <van-button class="follow-btn" round size="small" v-if="isFollowed" @click="onClick">已关注</van-button>
                <van-button
                v-else
              class="follow-btn"
              type="info"
              color="#3296fa"
              round
              size="small"
              icon="plus"
              @click="onClick"
              >关注</van-button
            >
</div>
</template>

<script>
import { deleteFollow, addFollow } from '@/api/user'
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';

export default {
// import引入的组件需要注入到对象中才能使用
  name: 'FollowUser',
  model: {
    prop: 'isFollowed',
    event: 'update-isFollowed'
  },
  props: {
    isFollowed: {
      type: Boolean,
      required: true
    },
    userId: {
      type: [String, Number],
      required: true
    }
  },
  components: {},
  data  () {
    // 这里存放数据
    return {
      isLoading: false
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 方法集合
  methods: {
    async onClick () {
      if (this.isLoading) return
      this.isLoading = true
      if (this.isFollowed) {
        // 取消
        await deleteFollow(this.userId)
        this.$emit('update-isFollowed', false)
      } else {
        // 关注
        await addFollow(this.userId)
        this.$emit('update-isFollowed', true)
      }
      this.isLoading = false
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created  () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {

  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类

</style>
