<!--  -->
<template>
  <div class="">
    <van-cell :title="item.aut_name">
      <van-image
        slot="icon"
        round
        width="30"
        height="30"
        style="margin-right: 10px;"
        :src="item.aut_photo"
      />
      <span style="color: #466b9d;" slot="title">{{ item.aut_name }}</span>
      <div slot="label">
        <p style="color: #363636;">{{ item.content }}</p>
        <p>
          <span style="margin-right: 10px;">{{ item.pubdate }}</span>
          <van-button size="mini" type="default" @click="articleIndex.onReplyShow(item)">回复</van-button>
        </p>
      </div>
      <van-icon
        slot="right-icon"
        :color="item.is_liking ? 'red' : '#323233'"
        :name="item.is_liking ? 'like' : 'like-o'"
        @click="onLike"
      />
    </van-cell>
  </div>
</template>

<script>
import { addCommentLike, deleteCommentLike } from '@/api/comment'
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';

export default {
// import引入的组件需要注入到对象中才能使用
  name: 'CommentItem',
  props: {
    item: Object
  },
  inject: ['articleIndex'],
  components: {},
  data  () {
    // 这里存放数据
    return {
      isLoading: false
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 方法集合
  methods: {
    async onLike () {
      if (this.isLoading) return
      this.isLoading = true
      this.item.is_liking
        ? await deleteCommentLike(this.item.com_id)
        : await addCommentLike(this.item.com_id)
      this.item.is_liking = !this.item.is_liking
      this.isLoading = false
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created  () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {

  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类
</style>
