<!--  -->
<template>
  <div class="">
    <van-nav-bar :title="comment.reply_count>0 ? comment.reply_count+'条评论':'评论详情'">
      <van-icon name="cross" slot="left" @click="$emit('close')"></van-icon>
    </van-nav-bar>
    <comment-item :item="comment"></comment-item>
    <van-cell title="全部回复"></van-cell>
    <!-- 使用评论列表 传类型,传评论id,添加组件别名 -->
    <article-comment type="c" :source="comment.com_id" ref="listRef"></article-comment>
    <div class="footer">
      <!-- 打开弹层  新增评论的评论弹层 -->
      <van-button round @click="isPostShow = true">写评论</van-button>
    </div>
    <!-- 传评论id和文章id -->
    <van-popup v-model="isPostShow" position="bottom">
    <!-- 光标放到post后面点击alt+/ -->
      <comment-post :target="comment.com_id" :articleId="articleIndex.articleId" @success="onSuccess"></comment-post>
    </van-popup>
  </div>
</template>

<script>
import CommentItem from './comment-item.vue'
import CommentPost from './comment-post.vue'
import ArticleComment from '@/views/article/comments/article-comment.vue'
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';

export default {
// import引入的组件需要注入到对象中才能使用
  name: 'CommentReply',
  inject: ['articleIndex'],
  props: { comment: Object },
  components: { CommentItem, CommentPost, ArticleComment },
  data  () {
    // 这里存放数据
    return {
      isPostShow: false
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 方法集合
  methods: {
    onSuccess (item) {
      // 关闭弹层
      this.isPostShow = false
      // 在添加评论成功后,添加到列表组件中
      this.$refs.listRef.list.unshift(item)
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created  () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {

  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类
.footer {
background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  .van-button {
    width: 400px;
  }

  padding: 20px 0;
}
</style>
