<template>
  <div class="article-container">
    <!-- 导航栏 -->
    <van-nav-bar class="page-nav-bar" left-arrow title="黑马头条"></van-nav-bar>
    <!-- /导航栏 -->

    <div class="main-wrap">
      <!-- 加载中 -->
      <div class="loading-wrap" v-if="isLoading">
        <van-loading color="#3296fa" vertical>加载中</van-loading>
      </div>
      <!-- /加载中 -->
      <template v-else>
        <!-- 加载完成-文章详情 -->
        <div class="article-detail" v-if="article.title">
          <!-- 文章标题 -->
          <h1 class="article-title">{{ article.title }}</h1>
          <!-- /文章标题 -->

          <!-- 用户信息 -->
          <van-cell class="user-info" center :border="false">
            <van-image
              class="avatar"
              slot="icon"
              round
              fit="cover"
              :src="article.aut_photo"
            />
            <div slot="title" class="user-name">{{ article.aut_name }}</div>
            <div slot="label" class="publish-date">{{ article.pubdate }}</div>
            <!-- 关注用户 -->
            <!-- <follow-user :is-follow="article.is_followed"
          @update-isFollowed="article.is_followed=$event" :userId="article.aut_id">
            </follow-user> -->
            <follow-user v-model="article.is_followed" :userId="article.aut_id">
            </follow-user>
            <!-- <van-button
              class="follow-btn"
              type="info"
              color="#3296fa"
              round
              size="small"
              icon="plus"
              >关注</van-button
            > -->
            <!-- <van-button
            class="follow-btn"
            round
            size="small"
          >已关注</van-button> -->
          </van-cell>
          <!-- /用户信息 -->

          <!-- 文章内容 -->
          <div
            ref="contentRef"
            class="article-content markdown-body"
            v-html="article.content"
          ></div>
          <van-divider>正文结束</van-divider>
          <article-comment :source="article.art_id"
          @set-total="totalComment=$event" ref="listRef"
          ></article-comment>
        </div>
        <!-- /加载完成-文章详情 -->
        <template v-else>
          <!-- 加载失败：404 -->
          <div class="error-wrap" v-if="errorStatus === 404">
            <van-icon name="failure" />
            <p class="text">该资源不存在或已删除！</p>
          </div>
          <!-- /加载失败：404 -->

          <!-- 加载失败：其它未知错误（例如网络原因或服务端异常） -->
          <div class="error-wrap" v-else>
            <van-icon name="failure" />
            <p class="text">内容加载失败！</p>
            <van-button class="retry-btn">点击重试</van-button>
          </div>
          <!-- /加载失败：其它未知错误（例如网络原因或服务端异常） -->
        </template>
      </template>
    </div>

    <!-- 底部区域 -->
    <div class="article-bottom" v-if="article.title">
      <van-button class="comment-btn" type="default" round size="small"
      @click="isPostShow=true"
        >写评论</van-button
      >
      <van-icon name="comment-o" :info="totalComment" color="#777" />
      <!-- <van-icon color="#777" name="star-o" /> -->
      <!-- <van-icon color="#777" name="good-job-o" /> -->
      <collect-article
        v-model="article.is_collected"
        :articleId="article.art_id"
      ></collect-article>
      <like-article
        v-model="article.attitude"
        :articleId="article.art_id"
      ></like-article>
      <van-icon name="share" color="#777"></van-icon>
    </div>
    <!-- /底部区域 -->
    <!-- 新增评论弹层 -->
    <van-popup v-model="isPostShow" position="bottom">
    <comment-post :target="article.art_id" @success="onSuccess"></comment-post>
    </van-popup>
    <!-- 查看评论回复弹层 -->
    <van-popup v-model="isReplyShow" position="bottom" :style="{height:'100%'}">
      <comment-reply @close="isReplyShow=false" :comment="currentComment"></comment-reply>
    </van-popup>
  </div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';
import { ImagePreview } from 'vant'
import { getArticleDetail } from '@/api/article.js'
import FollowUser from '@/components/follow-user'
import 'github-markdown-css/github-markdown.css'
import 'github-markdown-css/github-markdown-light.css'
import LikeArticle from '@/components/like-article.vue'
import CollectArticle from '@/components/collect-article.vue'
import ArticleComment from './comments/article-comment.vue'
import CommentPost from './comments/comment-post.vue'
import CommentReply from './comments/comment-reply.vue'
/* ImagePreview({
  images: [
    'https://img01.yzcdn.cn/vant/apple-1.jpg',
    'https://img01.yzcdn.cn/vant/apple-2.jpg'
  ],
  startPosition: 0
}) */
export default {
// import引入的组件需要注入到对象中才能使用
  name: 'ArticlePage',

  props: {
    articleId: {
      type: [Number, String],
      required: true
    }
  },
  components: { FollowUser, LikeArticle, CollectArticle, ArticleComment, CommentPost, CommentReply },
  data  () {
    // 这里存放数据
    return {
      article: {},
      isLoading: true,
      errorStatus: '',
      totalComment: 0,
      isPostShow: false, // 新增评论弹出显示
      isReplyShow: false, // 评论回复显示
      currentComment: {}
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},

  // 方法集合
  provide () {
    return {
      articleIndex: this
    }
  },
  methods: {
    onReplyShow (item) {
      // console.log(item)
      // 显示弹层
      this.isReplyShow = true
      // 保存查看的评论
      this.currentComment = item
    },
    onSuccess (item) {
      // 关闭弹性
      this.isPostShow = false
      // 把新的评论添加到列表里面-通过ref
      this.$refs.listRef.list.unshift(item)
    },
    async getArticleDetail () {
      try {
        // if (Math.random() > 0.5) {
        //   throw new function (status) {
        //     this.response = { status }
        //   }(500)
        // }
        const res = await getArticleDetail(this.articleId)
        this.article = res.data.data
        this.isLoading = false
        // 找到图片,绑定点击
        // document.querySelectorAll('img')
        console.log('this.$refs.contentRef', this.$refs.contentRef)
        this.$nextTick(() => {
          const images = []
          this.$refs.contentRef.querySelectorAll('img')
            .forEach((t, i) => {
              images.push(t.src)
              t.onclick = () => {
                ImagePreview({
                  images,
                  startPosition: i
                })
              }
            })
        })
      } catch (e) {
        this.isLoading = false
        this.errorStatus = e.response.status
        // 没有找到
        // 服务器出问题
        console.log(e)
      }
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created  () {
    this.getArticleDetail()
  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {
    // console.log(this.$route.params.articleId)
    // console.log(this.articleId)
  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类
.article-container {
  .main-wrap {
    position: fixed;
    left: 0;
    right: 0;
    top: 92px;
    bottom: 88px;
    overflow-y: scroll;
    background-color: #fff;
  }
  .article-detail {
    .article-title {
      font-size: 40px;
      padding: 50px 32px;
      margin: 0;
      color: #3a3a3a;
    }

    .user-info {
      padding: 0 32px;
      .avatar {
        width: 70px;
        height: 70px;
        margin-right: 17px;
      }
      .van-cell__label {
        margin-top: 0;
      }
      .user-name {
        font-size: 24px;
        color: #3a3a3a;
      }
      .publish-date {
        font-size: 23px;
        color: #b7b7b7;
      }
      .follow-btn {
        width: 170px;
        height: 58px;
      }
    }

    .article-content {
      padding: 55px 32px;
      /deep/ p {
        text-align: justify;
      }
    }
  }

  .loading-wrap {
    padding: 200px 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
  }

  .error-wrap {
    padding: 200px 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    .van-icon {
      font-size: 122px;
      color: #b4b4b4;
    }
    .text {
      font-size: 30px;
      color: #666666;
      margin: 33px 0 46px;
    }
    .retry-btn {
      width: 280px;
      height: 70px;
      line-height: 70px;
      border: 1px solid #c3c3c3;
      font-size: 30px;
      color: #666666;
    }
  }

  .article-bottom {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-sizing: border-box;
    height: 88px;
    border-top: 1px solid #d8d8d8;
    background-color: #fff;
    .comment-btn {
      width: 282px;
      height: 46px;
      border: 2px solid #eeeeee;
      font-size: 30px;
      line-height: 46px;
      color: #a7a7a7;
    }
    .van-icon {
      font-size: 40px;
      .van-info {
        font-size: 16px;
        background-color: #e22829;
      }
    }
  }
}
</style>
