<!--  -->
<template>
  <div class="">
    <van-icon
      :color="value === 1 ? '#e5645f' : '#777'"
      :name="value === 1 ? 'good-job' : 'good-job-o'"
      @click="onClick"
    ></van-icon>
  </div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';

import { deleteLike, addLike } from '@/api/article.js'
export default {
// import引入的组件需要注入到对象中才能使用
  name: 'LikeArticle',
  props: {
    value: {
      type: Number,
      required: true
    },
    articleId: {
      type: [Number, String],
      required: true
    }
  },
  components: {},
  data  () {
    // 这里存放数据
    return {
      isLoading: false
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 方法集合
  methods: {
    async onClick () {
      if (this.isLoading) return
      this.isLoading = true
      if (this.value === 1) {
        // 取消
        await deleteLike(this.articleId)
        this.$emit('input', 0)
      } else {
        // 点赞
        await addLike(this.articleId)
        this.$emit('input', 1)
      }
      this.isLoading = false
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created  () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {

  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类
</style>
